@font-face{
 font-family: "overpassVar";
 src: url("Inconsolata-VariableFont_wdth,wght.ttf")
}

*{
  user-select: none !important;
  background-color: black; 
  color: white; 
  font-family: "overpassVar" !important;
  overflow: hidden;
  text-align: left;
}

#menu {
  
  display: inline;
  float: left;
  width: 32.51310993871px;
  height: 100%;
  border-right: 1px solid #484848;
}

#menu-button { 
  width: 100%;
  height: 10px;
  background-color: black;
  padding-bottom: 7px;
  padding-top: 5px;
  border-bottom: 1px solid #484848;
}

#menu-group{
  top: 23.606797749979%;
  width: 32.51310993871px;
  position: fixed; 
}

#menu-spacer { 
  width: 100%;
  height: auto;
  background-color: black;
  padding-bottom: 5px;
  padding-top: 5px;
}

#vitruv {
  width: 100%;
  height: auto;
}

#killphrase {
  position: fixed;
  left: 38px;
  top: 7.5px;
}

#page {
  padding: 15px;
  position: fixed; 
  width: 50%;
  height: 75%;
  top: 10%;
  display: inline-block;
  float: center;
  left: 25%;
  border: 1px solid #484848;
}

.selector-bar {
  height: 23px;
  text-align: center;
  vertical-align: middle;
  float: center;
  width: 30%;
  background: rgba(0,0,0,0);
}

#bar {
  background-color: #484848;
  height: 1px;
  width: 100%;
  float: center;
  margin-top: 2px;
}

#menu-button:hover {
  background: linear-gradient(269deg, #535353, #000000);     
  color: black !important;
  background-size: 400% 400%;     
  -webkit-animation: bar-selection 1s ease-in 1;     
  -moz-animation: bar-selection 1s ease-in 1;     
  animation: bar-selection 1s ease-out 1;
  -webkit-fill-mode: forwards;
  -moz-fill-mode: forwards;
  animation-fill-mode: forwards;

}

@-webkit-keyframes bar-selection {     
  0%{background-position:0% 0%}     
  100%{background-position:100% 56%}     
} 
@-moz-keyframes bar-selection {    
  0%{background-position:0% 0%}     
  100%{background-position:100% 56%}     
} 
@keyframes bar-selection {     
  0%{background-position:0% 0%}     
  100%{background-position:100% 56%}     
}

#bar-group {
  margin-top: 8px;
  position: fixed;
  top: 23.606%;
  left: 32.513px;
  background: rgba(0,0,0,0);
}


svg{
  background: rgba(0, 0, 0, 0);
}

#head {
  width: 100%;
  height: auto;
  padding-top: 3.4441853748634px;
  padding-bottom: 3.4441853748634px;
  border-bottom: 1px solid #484848;
}

#menu-object, html, body, div {
  width: 100%;
  height: 100%;
}

.App { 
  text-align: center;
}

#blur {
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #484848;
}

@-webkit-keyframes background-scroll {     
  0%{background-position:0% 59%}     
  50%{background-position:100% 42%}     
  100%{background-position:0% 59%} 
} 
@-moz-keyframes background-scroll {     
  0%{background-position:0% 59%}     
  50%{background-position:100% 42%}     
  100%{background-position:0% 59%} 
}
@keyframes background-scroll {     
  0%{background-position:0% 59%}     
  50%{background-position:100% 42%}     
  100%{background-position:0% 59%}
}

#close:hover {

  background: linear-gradient(269deg, #C3423C, #000000);     
  
  color: black !important;
  background-size: 400% 400%;     
  -webkit-animation: bar-selection 1s ease-in 1;     
  -moz-animation: bar-selection 1s ease-in 1;     
  animation: bar-selection 1s ease-out 1;
  -webkit-fill-mode: forwards;
  -moz-fill-mode: forwards;
  animation-fill-mode: forwards;
  
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
